<template>
  <div>
    <MainPage v-if="!sockData" msg="Socks made by you:" @generatedSock="showSock"/>
    <SockDetails v-else :name="sockData.name" :description="sockData.description" :type="sockData.type" :slogan="sockData.slogan" />
  </div>
</template>

<script>
import MainPage from './components/MainPage.vue'
import SockDetails from './components/SockDetails.vue'

export default {
  name: 'App',
  components: {
    MainPage,
    SockDetails
  },
  data() {
    return {
      sockData: null
    }
  },
  methods: {
    showSock(sockCSV) {
      const [name, type, slogan, description] = sockCSV.split(';').map(str => str.trim());

      this.sockData = {
        name,
        type,
        slogan,
        description
      };
    }
  }
}
</script>

<style>
#app {
  font-family: Avenir, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>
